import React, { useState, useEffect, useCallback } from 'react';
import isEmail from 'validator/es/lib/isEmail';
import { checkPhoneNumber, isRegionExistInArray } from '../../utils/utils';
import { regions } from '../../utils/constants';

import { Title, Input, Checkbox, CheckboxTxt, Select } from '../../components';

import useFormValidation from '../../hooks/useFormValidation';
import { useTelegram } from '../../hooks/useTelegram.js';

import './Registration.css';

export default function Registration() {
  const [check, setCheck] = useState(false);
  const [isFormValid, setIsFormValid] = useState(true);
  const [errors, setErrors] = useState([]);
  const [isRegionsOpen, setIsRegionsOpen] = useState(false);
  const { handleChange, values, setValues } = useFormValidation();
  const { tg } = useTelegram();

  const onSendData = useCallback(() => {
    const checkValidity = () => {
      const errorsArr = [];
      if (
        (values['firstName'] &&
          !/\s*^[а-яёa-z -/']+\s*$/iu.test(values['firstName'])) ||
        !values['firstName']
      ) {
        errorsArr.push('firstName');
      }
      if (
        (values['lastName'] &&
          !/\s*^[а-яёa-z -/']+\s*$/iu.test(values['lastName'])) ||
        !values['lastName']
      ) {
        errorsArr.push('lastName');
      }
      if (!checkPhoneNumber(values['phone']) || !values['phone']) {
        errorsArr.push('phone');
      }
      if ((values['mail'] && !isEmail(values['mail'])) || !values['mail']) {
        errorsArr.push('mail');
      }
      if (!check) errorsArr.push('agree');
      if (!values['region'] || isRegionExistInArray(regions, values['region'])) {
        errorsArr.push('region');
      }

      setErrors(errorsArr);
      if (errorsArr.length === 0 && check) {
        return true;
      } else {
        return false;
      }
    };

    if (checkValidity()) {
      console.log('Valid');
      setIsFormValid(true);
      const data = { formType: 'reg', ...values };
      tg.sendData(JSON.stringify(data));
    } else {
      console.log('Invalid');
      setIsFormValid(false);
    }
  }, [values, tg, check]);

  useEffect(() => {
    if (!isFormValid) {
      const offset =
        document
          .querySelector('.input__field_incorrect')
          ?.getBoundingClientRect()?.top +
        window.pageYOffset -
        40;
      window.scrollTo(0, offset);
    }
  }, [errors, isFormValid]);

  useEffect(() => {
    tg.onEvent('mainButtonClicked', onSendData);
    return () => {
      tg.offEvent('mainButtonClicked', onSendData);
    };
  }, [onSendData, tg]);

  useEffect(() => {
    tg.MainButton.show();
    tg.MainButton.setParams({
      text: 'Зарегистрироваться',
      color: '#4e5994',
    });
  }, [tg.MainButton]);

  const onFocus = () => {
    setIsRegionsOpen(true);
  };

  const onBlur = () => {
    setTimeout(() => {
      setIsRegionsOpen(false);
    }, 100);
  };

  const onClickRegion = (selectedRegion) => {
    console.log(selectedRegion);
    setValues({ ...values, region: selectedRegion });
  };

  return (
    <>
      <form
        className="registration"
        noValidate
        onSubmit={(e) => e.preventDefault()}
      >
        <div className="registration__header">
          <Title>Регистрация</Title>
          <Input
            label="Фамилия"
            placeholder="Иванов"
            name="lastName"
            onChange={handleChange}
            values={values}
            setValues={setValues}
            type="text"
            marked
            errors={errors}
          />
          <Input
            label="Имя"
            placeholder="Иван"
            name="firstName"
            onChange={handleChange}
            values={values}
            setValues={setValues}
            type="text"
            marked
            errors={errors}
          />
          <Input
            label="Отчество"
            placeholder="Иванович"
            name="middleName"
            onChange={handleChange}
            values={values}
            setValues={setValues}
            type="text"
            errors={errors}
          />
          <div className="regionsWrapper">
            <Input
              label="Регион"
              name="region"
              onChange={handleChange}
              values={values}
              setValues={setValues}
              type="text"
              marked
              errors={errors}
              onFocus={onFocus}
              onBlur={onBlur}
            />
            <Select
              items={regions}
              isOpen={isRegionsOpen}
              searchQuery={values['region']}
              handleClick={onClickRegion}
            />
          </div>
          <Input
            label="Номер телефона"
            name="phone"
            onChange={handleChange}
            values={values}
            setValues={setValues}
            type="tel"
            placeholder={'Формат: 79991236655'}
            marked
            errors={errors}
          />
          <Input
            label="Почта"
            name="mail"
            onChange={handleChange}
            values={values}
            setValues={setValues}
            type="email"
            marked
            errors={errors}
          />
          <Checkbox
            text={<CheckboxTxt />}
            id="agree"
            name="agree"
            onChange={() => setCheck(!check)}
            value={check}
            errors={errors}
          />
          {/* <button onClick={onSendData}>Проверить</button> */}
          <span
            className={`registration__note ${
              !isFormValid && 'registration__note_visible'
            }`}
          >
            Пожалуйста, проверьте правильность заполнения полей, отмеченные
            звездочкой (<span className="marked">*</span>)
          </span>
        </div>
      </form>
    </>
  );
}
