const checkPhoneNumber = (number) => {
  const phoneNumberRegex = /^7\d{10}$/;
  return phoneNumberRegex.test(Number(number));
};

const isRegionExistInArray = (array, query) => {
  return (
    array
      .map((region) => region.name.toLowerCase())
      .findIndex((reg) => reg === query.toLowerCase()) < 0
  );
};

export { checkPhoneNumber, isRegionExistInArray };
